
.header {
  background: transparent no-repeat center -10px;
  position: relative;
  margin: -42px auto 0;
}
.header .header-layer height 170px {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  z-index: 10;
}
.header .header-link {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 170px;
  z-index: 10;
}
.header .h-center {
  width: 960px;
  margin: 0 auto;
  position: relative;
  height: 170px;
  transition: 0.2s height;
}
.header .h-center .logo {
  position: absolute;
  width: 220px;
  height: 105px;
  left: 24px;
  top: 55px;
  background: transparent no-repeat left center;
  z-index: 100;
}
.header .h-center .banner-title {
  position: absolute;
  top: 114px;
  left: 255px;
  line-height: 20px;
  padding: 6px 10px;
  background-color: rgba(0,0,0,0.68);
  color: #fff;
  border-radius: 4px;
  font-size: 14px;
  max-width: 350px;
  transition: 0.2s;
}


