
.top_container {
  color: #222;
  position: relative;
  z-index: 10000;
}
.top_container .popContainer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.3);
}
.top_container .z_top {
  background-color: transparent;
  height: 42px;
  background: #fff;
  box-shadow: rgba(0,0,0,0.1) 0 1px 2px;
  top: 0px;
  left: 0px;
  width: 100%;
  position: relative;
  z-index: 10000;
  font-family: "Microsoft YaHei", Arial, Helvetica, sans-serifsans-serif;
  color: #222;
}
.top_container .z_top .i-link {
  padding: 0 14px;
  transition: 0.15s background-color;
  font-size: 13px;
}
.top_container .z_top a {
  color: #222;
}
.top_container .b-header-blur {
  background-color: transparent;
}
.top_container .b-header-blur .b-header-mask-wrp {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #fff;
}
.top_container .b-header-blur .b-header-mask-wrp .b-header-mask-bg {
  position: absolute;
  height: 62px;
  padding: 0 20px;
  top: -10px;
  left: -20px;
  width: 100%;
  filter: blur(5px);
  z-index: 50;
  background-position: center 0 !important;
  background-repeat: no-repeat;
}
.top_container .b-header-blur .b-header-mask-wrp .b-header-mask {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(0,0,0,0.4);
}
.top_container .b-header-blur .z_header {
  display: block;
  margin: 0 auto;
  position: relative;
  text-align: left;
  z-index: 10000;
  width: 980px;
  zoom: 1;
}
.top_container .b-header-blur .z_header:after {
  content: '';
  display: block;
  visibility: hidden;
  height: 0;
  clear: both;
  font-size: 0;
}
.top_container .b-header-blur .z_header .z_top_nav {
  float: left;
  height: 42px;
  font-size: 12px;
}
.top_container .b-header-blur .z_header .z_top_nav li .new {
  position: absolute;
  right: -8px;
  color: #f25d8e;
  top: 13px;
  background: url("../../assets/images/icons.png") no-repeat;
  width: 22px;
  height: 10px;
  background-position: -851px -412px;
}
.top_container .b-header-blur .z_header .z_top_nav li a.i-link {
  font-size: 12px;
}
.top_container .b-header-blur .z_header .z_top_nav ul {
  zoom: 1;
}
.top_container .b-header-blur .z_header .z_top_nav ul:after {
  content: '';
  display: block;
  visibility: hidden;
  height: 0;
  clear: both;
  font-size: 0;
}
.top_container .b-header-blur .z_header .z_top_nav ul li {
  float: left;
  text-align: center;
  line-height: 42px;
  position: relative;
  list-style-stype: none;
}
.top_container .b-header-blur .z_header .z_top_nav ul li a.i-link {
  height: 100%;
  display: block;
  color: #fff;
}
.top_container .b-header-blur .z_header .z_top_nav ul li.home {
  margin-left: -10px;
  background-image: url("../../assets/images/icons.png");
  background-repeat: no-repeat;
  background-position: -845px -74px;
}
.top_container .b-header-blur .z_header .z_top_nav ul li.home .i-link {
  width: 50px;
  height: 42px;
  padding: 0 0 0 20px;
}
.top_container .b-header-blur .z_header .z_top_nav ul li.hbili {
  position: relative;
}
.top_container .b-header-blur .z_header .z_top_nav ul li.live {
  position: relative;
}
.top_container .b-header-blur .z_header .uns_box {
  float: right;
  font-size: 12px;
}
.top_container .b-header-blur .z_header .uns_box ul.menu {
  float: left;
  position: relative;
}
.top_container .b-header-blur .z_header .uns_box ul li {
  list-style-type: none;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i {
  float: left;
  text-align: center;
  height: 42px;
  line-height: 42px;
  position: relative;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i a.i-link {
  display: block;
  color: #222;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i#i_menu_login_reg {
  padding: 0 10px;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i#i_menu_login_reg .i-link {
  display: inline-block;
  padding: 0 10px;
  color: #fff;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i#i_menu_login_reg .i-link span {
  display: block;
  margin: 0px;
  padding: 0px;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i#i_menu_login_reg .s-line {
  display: inline-block;
  border-left: 1px solid #fff;
  height: 12px;
  margin-top: 16px;
  vertical-align: top;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i.b-post {
  margin-right: 0px;
}
.top_container .b-header-blur .z_header .uns_box ul li.u-i.b-post a.i-link {
  margin: 0px;
  padding: 0px;
  width: 58px;
  position: relative;
  z-index: 100;
  background: url("../../assets/images/b-post.png") center center no-repeat;
  height: 48px;
  border-radius: 0 0 5px 5px;
  color: #fff !important;
  font-size: 14px;
}


